// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import {
  getFirestore,
  collection,
  doc,
  setDoc,
  getDoc,
  deleteDoc,
  query,
  getDocs,
} from "firebase/firestore";

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyBM5kWHzS7Uj57NZFj0V3xDWQqM9vOhGPw",
  authDomain: "flashcards-app-19dff.firebaseapp.com",
  projectId: "flashcards-app-19dff",
  storageBucket: "flashcards-app-19dff.firebasestorage.app",
  messagingSenderId: "314606068679",
  appId: "1:314606068679:web:5286f9fc0157a059086fe2",
  measurementId: "G-F3LDEXY4TG",
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);

// --- Firestore Functions ---

/**
 * Save a flashcard deck to Firestore
 * @param {string} deckId - Unique deck identifier
 * @param {array} cards - Array of flashcard objects
 * @param {string} name - Deck name (optional)
 */
export async function saveDeck(deckId, cards, name = "My Deck") {
  try {
    await setDoc(doc(db, "decks", deckId), {
      name,
      cards,
      createdAt: new Date(),
      updatedAt: new Date(),
    });
    return deckId;
  } catch (error) {
    console.error("Error saving deck:", error);
    throw error;
  }
}

/**
 * Load a flashcard deck from Firestore by ID
 * @param {string} deckId - Deck identifier
 */
export async function loadDeck(deckId) {
  try {
    const docSnap = await getDoc(doc(db, "decks", deckId));
    if (docSnap.exists()) {
      return { id: deckId, ...docSnap.data() };
    } else {
      throw new Error("Deck not found");
    }
  } catch (error) {
    console.error("Error loading deck:", error);
    throw error;
  }
}

/**
 * Get all decks (list all saved decks)
 */
export async function getAllDecks() {
  try {
    const q = query(collection(db, "decks"));
    const querySnapshot = await getDocs(q);
    const decks = [];
    querySnapshot.forEach((doc) => {
      decks.push({ id: doc.id, ...doc.data() });
    });
    return decks;
  } catch (error) {
    console.error("Error getting decks:", error);
    throw error;
  }
}

/**
 * Delete a deck from Firestore
 * @param {string} deckId - Deck identifier
 */
export async function deleteDeck(deckId) {
  try {
    await deleteDoc(doc(db, "decks", deckId));
  } catch (error) {
    console.error("Error deleting deck:", error);
    throw error;
  }
}

// --- localStorage Functions (Progress Tracking) ---

const PROGRESS_KEY = "flashcards_progress";

/**
 * Save progress to localStorage
 * @param {string} deckId - Deck identifier
 * @param {array} correctIds - IDs of correct cards
 * @param {array} wrongIds - IDs of wrong cards
 */
export function saveProgress(deckId, correctIds, wrongIds) {
  try {
    const progress = getProgress();
    progress[deckId] = {
      correctIds,
      wrongIds,
      lastUpdated: new Date().toISOString(),
    };
    localStorage.setItem(PROGRESS_KEY, JSON.stringify(progress));
  } catch (error) {
    console.error("Error saving progress:", error);
  }
}

/**
 * Load progress from localStorage
 * @param {string} deckId - Deck identifier
 */
export function loadProgress(deckId) {
  try {
    const progress = getProgress();
    return progress[deckId] || { correctIds: [], wrongIds: [] };
  } catch (error) {
    console.error("Error loading progress:", error);
    return { correctIds: [], wrongIds: [] };
  }
}

/**
 * Get all progress data
 */
function getProgress() {
  try {
    const data = localStorage.getItem(PROGRESS_KEY);
    return data ? JSON.parse(data) : {};
  } catch (error) {
    console.error("Error reading progress:", error);
    return {};
  }
}

/**
 * Clear progress for a specific deck
 */
export function clearProgress(deckId) {
  try {
    const progress = getProgress();
    delete progress[deckId];
    localStorage.setItem(PROGRESS_KEY, JSON.stringify(progress));
  } catch (error) {
    console.error("Error clearing progress:", error);
  }
}

/**
 * Generate a shareable deck ID
 */
export function generateDeckId() {
  return `deck_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
}

// --- Session State (current studying session) ---

const SESSION_KEY = "flashcards_session";

/**
 * Save current session state
 * @param {string} deckId - Current deck ID
 * @param {number} idx - Current card index
 * @param {array} correctIds - Correct card IDs so far
 * @param {array} wrongIds - Wrong card IDs so far
 * @param {array} poolIds - Ordered IDs of the current study pool
 */
export function saveSession(deckId, idx, correctIds, wrongIds, poolIds) {
  try {
    const session = {
      deckId,
      idx,
      correctIds,
      wrongIds,
      poolIds,
      savedAt: new Date().toISOString(),
    };
    localStorage.setItem(SESSION_KEY, JSON.stringify(session));
  } catch (error) {
    console.error("Error saving session:", error);
  }
}

/**
 * Load current session state
 */
export function loadSession() {
  try {
    const data = localStorage.getItem(SESSION_KEY);
    return data ? JSON.parse(data) : null;
  } catch (error) {
    console.error("Error loading session:", error);
    return null;
  }
}

/**
 * Clear session state
 */
export function clearSession() {
  try {
    localStorage.removeItem(SESSION_KEY);
  } catch (error) {
    console.error("Error clearing session:", error);
  }
}

// --- Subset Selection (for quick restoration) ---

const SUBSET_KEY = "flashcards_subset";

/**
 * Save subset selection for quick restoration
 * @param {string} deckId - Deck ID
 * @param {object} selection - Selection object (id -> boolean)
 */
export function saveSubsetSelection(deckId, selection) {
  try {
    const subsets = getSubsetSelections();
    subsets[deckId] = {
      selection,
      savedAt: new Date().toISOString(),
    };
    localStorage.setItem(SUBSET_KEY, JSON.stringify(subsets));
  } catch (error) {
    console.error("Error saving subset selection:", error);
  }
}

/**
 * Load subset selection for a deck
 * @param {string} deckId - Deck ID
 */
export function loadSubsetSelection(deckId) {
  try {
    const subsets = getSubsetSelections();
    return subsets[deckId]?.selection || {};
  } catch (error) {
    console.error("Error loading subset selection:", error);
    return {};
  }
}

/**
 * Get all subset selections
 */
function getSubsetSelections() {
  try {
    const data = localStorage.getItem(SUBSET_KEY);
    return data ? JSON.parse(data) : {};
  } catch (error) {
    console.error("Error reading subset selections:", error);
    return {};
  }
}
